@font-face {
  font-family: "Programme";
  src: url("https://assets.genius.com/fonts/programme_bold.woff2") format("woff2");
}
@keyframes gb-opacity-in {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}
@keyframes gb-opacity-out {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
@keyframes gb-zoom-in {
  0% {
    transform: scale(0, 0);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes gb-zoom-out {
  0% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(0, 0);
  }
}
@keyframes gb-slide-rtl {
  from {
    right: 35px;
    transform: scale(0.2, 0.2);
    opacity: 0;
  }
  to {
    right: 122px;
    transform: scale(1, 1);
    opacity: 1;
  }
}
.gb-fade-in {
  animation: gb-opacity-in 0.5s ease;
}

.gb-fade-out {
  animation: gb-opacity-out 0.4s ease;
}

.gb-zoom-in {
  animation: gb-zoom-in 0.4s ease;
}

.gb-zoom-out {
  animation: gb-zoom-out 0.3 ease;
}

.gb-animate-right {
  position: relative;
  animation: gb-slide-rtl 0.4s;
}

.disable-scrolling {
  height: 100%;
  overflow: hidden;
}

